	module.exports = {"data":{"markdownRemark":{"html":"<p>It is particularly hot and seems to be extraordinary. The urban climate has begun to turn hot, I wonder if it is the result of economic development, especially the large increase in air conditioners, which emits amazing heat and the environmental impact can be imagined.</p>\n<p>The newspaper reported that the death of President Kim Il Sung of North Korea may mean the beginning of a new era. There have been a series of problems recently, mainly within the international community and within the two North Koreans. What kind of impact this incident will have on the development of these problems is to be observed. Maybe there will be soon</p>\n<p>New developments, from the perspective of domestic political stability.</p>\n<p>It rained in the evening and the weather was much cooler. Going out for a walk at night, get a little poem:</p>\n<p>Night Tour Chang'an Street</p>\n<p>The rain fell and the net was in the capital, and the green wall was red. Walk light and sway, and join hands to condense the arch. It means that the world is light and laughs at life. The passengers are in a hurry and lonely, and they know what they know.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0120.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0120.md","frontmatter":{"title":"Sunday on July 10th"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0120.md absPath of file >>> MarkdownRemark"}}

